@import "~antd-mobile/dist/antd-mobile.css";
/*共有样式文件*/
.df {
  display: flex;
  flex-direction: column;
}
.rf {
  display: flex;
  flex-direction: row;
}
.jcc {
  justify-content: center;
}
.jcsb {
  justify-content: space-between;
}
.jcsa {
  justify-content: space-around;
}
.jcfs {
  justify-content: flex-start;
}
.jcfe {
  justify-content: flex-end;
}
.aic {
  align-items: center;
}
.aifs {
  align-items: flex-start;
}
.aife {
  align-items: flex-end;
}
/* 去除a标签默认 */
a {
  text-decoration: none;
}
p {
  margin-block-start: 0;
  margin-block-end: 0;
}
input {
  outline: none;
  -webkit-appearance: none;
  border: none;
  caret-color: black;
}
select {
  outline: none;
  -webkit-appearance: none;
  border: none;
}
button {
  outline: none;
  -webkit-appearance: none;
  border: none;
}
.am-list-body {
  position: relative;
  background-color: transparent;
}
.am-list-body::before {
  height: 0 !important;
}
.am-list-body::after {
  height: 0 !important;
}
.am-list-line::after {
  height: 0 !important;
}
.am-list-body .am-list-item:last-child {
  border: 1px solid #ccc;
}
.mul-container {
  background-color: #fff;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  width: 100vw;
  height: 100vh;
}
.mul-header {
  background-color: transparent;
  background-image: url("../img/hc-logo.png");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  width: 3.19992rem;
  height: 3.19992rem;
  margin: 0 auto 20px;
}
.mul-header-one {
  height: 0.79998rem;
}
.mul-mobile-input {
  position: relative;
}
.mul-mobile-text {
  position: absolute;
  top: 0;
  right: 0.53332rem;
  bottom: 0;
  display: flex;
  align-items: center;
  margin-right: 0.373324rem;
  font-size: 0.39999rem;
  color: #2e5ef5;
}
.mul-Code {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  border: 1px solid #ccc;
  border-radius: 4px;
  background-color: transparent;
  margin: 0.26666rem 0.53332rem;
}
.mul-Code-text {
  margin-right: 0.373324rem;
  font-size: 0.39999rem;
  font-family: "PingFang-SC";
}
.mul-Code-input {
  width: 4.66655rem;
  background-color: transparent;
}
.mul-input {
  background-color: transparent;
}
.mul-input-item {
  margin: 0.293326rem 0.53332rem 0.26666rem;
  box-sizing: border-box;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-bottom: 10px;
  background-color: #fff;
}
.mul-input-item-success {
  margin: 0.293326rem 0.53332rem 0.26666rem;
  box-sizing: border-box;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-bottom: 10px;
  margin-top: 1.19997rem;
  background-color: transparent;
}
.mul-input .success-title {
  font-size: 0.66665rem;
  font-family: "PingFang-SC";
  text-align: center;
  font-weight: "bold";
  margin-top: 0.66665rem;
}
.mul-input .success-text {
  font-size: 0.39999rem;
  font-family: "PingFang-SC";
  text-align: center;
  margin-top: 0.26666rem;
}
.mul-button {
  height: 1.3333rem;
  line-height: 1.3333rem;
  text-align: center;
  margin: 1.06664rem 0.53332rem 0;
  border-radius: 0.66665rem;
  background-color: #2e5ef5;
}
.mul-button:hover {
  opacity: 0.8;
}
.mul-button p {
  font-size: 0.453322rem;
  font-weight: 500;
  color: #fff;
}
.mul-button-success {
  height: 1.3333rem;
  line-height: 1.3333rem;
  text-align: center;
  margin: 1.59996rem 0.53332rem 0;
  border-radius: 0.66665rem;
  background-color: #2e5ef5;
}
.mul-button-success p {
  font-size: 0.453322rem;
  font-weight: 500;
  color: #fff;
}
.mul-footer {
  display: flex;
  justify-content: center;
  margin: 0.53332rem 0.53332rem 0;
  font-size: 0.39999rem;
  color: #999;
}
.mul-dw {
  position: fixed;
  bottom: 0;
  background-color: rgba(46, 94, 245, 0.1);
  display: flex;
  flex-direction: row;
  padding: 0.4rem 0.53332rem 0;
  height: 2.6666rem;
  width: 100vw;
  justify-content: space-between;
  box-sizing: border-box;
}
.mul-dw-text {
  font-size: 0.39999rem;
  color: #2e5ef5;
  line-height: 0.906644rem;
}
.mul-dw-btn {
  font-size: 0.39999rem;
  width: 2.13328rem;
  height: 0.906644rem;
  line-height: 0.906644rem;
  background-color: #2e5ef5;
  border-radius: 0.106664rem;
  text-align: center;
  color: #fff;
}
.mul-modal {
  position: fixed;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1;
}
.mul-modal-dw {
  background-image: url("../img/hc-success.png");
  width: 8.53312rem;
  height: 8.79978rem;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
.mul-modal-dw-text {
  font-size: 0.39999rem;
  text-align: center;
}
.mul-modal-dw-text p {
  margin-top: 0.213328rem;
}
.mul-modal-dw-text .dddd {
  margin-top: 0.053332rem;
}
.mul-modal-dw-text p:first-child {
  font-size: 0.53332rem;
  margin-top: 0.79998rem;
}
.mul-modal-dw-text p:last-child {
  font-size: 1.06664rem;
  margin-top: 1.06664rem;
}
.mul-modal-dw-btn {
  margin-bottom: 0.479988rem;
  width: 6.6665rem;
  height: 1.3333rem;
  line-height: 1.3333rem;
  border-radius: 0.53332rem;
  text-align: center;
  font-size: 0.453322rem;
}
.mul-modal-qq {
  position: absolute;
  top: 0;
  right: 0;
}
.mul-modal-qq-img {
  height: 2.693266rem;
  width: 4.79988rem;
}
