@import "~antd-mobile/dist/antd-mobile.css";
@import "common.less";

.am-list-body {
  position: relative;
  background-color: transparent;
}
.am-list-body::before {
  height: 0 !important;
}
.am-list-body::after {
  height: 0 !important;
}
.am-list-line::after {
  height: 0 !important;
}
.am-list-body .am-list-item:last-child {
  border: 1px solid #ccc;
}

.mul-container {
  background-color: #fff;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  width: 100vw;
  height: 100vh;
}

.mul-header {
  background-color: transparent;
  background-image: url("../img/hc-logo.png");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  width: 24 * @px-10;
  height: 24 * @px-10;
  margin: 0 auto 20px;
  &-one {
    height: 6 * @px-10;
  }
}

.mul-mobile-input {
  position: relative;
}

.mul-mobile-text {
  position: absolute;
  top: 0;
  right: 4 * @px-10;
  bottom: 0;
  display: flex;
  align-items: center;
  margin-right: 2.8 * @px-10;
  font-size: 3 * @px-10;
  color: #2e5ef5;
}

.mul-Code {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  border: 1px solid #ccc;
  border-radius: 4px;
  // height: 9.2*@px-10;
  // line-height: 9.2*@px-10;
  background-color: transparent;
  margin: 2 * @px-10 4 * @px-10;
  &-text {
    // margin-left: 2*@px-10;
    margin-right: 2.8 * @px-10;
    font-size: 3 * @px-10;
    font-family: "PingFang-SC";
  }
  &-input {
    width: 35 * @px-10;
    background-color: transparent;
  }
}

.mul-input {
  background-color: transparent;
  &-item {
    // height: 10*@px-10;
    // line-height: 10*@px-10;
    margin: 2.2 * @px-10 4 * @px-10 2 * @px-10;
    box-sizing: border-box;
    border: 1px solid #ccc;
    border-radius: 4px;
    margin-bottom: 10px;
    background-color: #fff;
  }
  &-item-success {
    // height: 9.2*@px-10;
    // line-height: 9.2*@px-10;
    margin: 2.2 * @px-10 4 * @px-10 2 * @px-10;
    box-sizing: border-box;
    border: 1px solid #ccc;
    border-radius: 4px;
    margin-bottom: 10px;
    margin-top: 9 * @px-10;
    background-color: transparent;
  }
  .success-title {
    font-size: 5 * @px-10;
    font-family: "PingFang-SC";
    text-align: center;
    font-weight: "bold";
    margin-top: 5 * @px-10;
  }
  .success-text {
    font-size: 3 * @px-10;
    font-family: "PingFang-SC";
    text-align: center;
    margin-top: 2 * @px-10;
  }
}

.mul-button {
  height: 10 * @px-10;
  line-height: 10 * @px-10;
  text-align: center;
  margin: 8 * @px-10 4 * @px-10 0;
  border-radius: 5 * @px-10;
  background-color: #2e5ef5;

  &:hover {
    opacity: 0.8;
  }

  p {
    font-size: 3.4 * @px-10;
    font-weight: 500;
    color: #fff;
  }
}

.mul-button-success {
  height: 10 * @px-10;
  line-height: 10 * @px-10;
  text-align: center;
  margin: 12 * @px-10 4 * @px-10 0;
  border-radius: 5 * @px-10;
  background-color: #2e5ef5;
  p {
    font-size: 3.4 * @px-10;
    font-weight: 500;
    color: #fff;
  }
}

.mul-footer {
  display: flex;
  justify-content: center;
  margin: 4 * @px-10 4 * @px-10 0;
  font-size: 3 * @px-10;
  color: #999;
}

.mul-dw {
  position: fixed;
  bottom: 0;
  // z-index: 2;
  // bottom: 0;
  background-color: rgba(46, 94, 245, 0.1);
  display: flex;
  flex-direction: row;
  padding: 0.4rem 4 * @px-10 0;
  height: 20 * @px-10;
  width: 100vw;
  justify-content: space-between;
  box-sizing: border-box;
  &-text {
    font-size: 3 * @px-10;
    color: #2e5ef5;
    line-height: 6.8 * @px-10;
  }
  &-btn {
    font-size: 3 * @px-10;
    width: 16 * @px-10;
    height: 6.8 * @px-10;
    line-height: 6.8 * @px-10;
    background-color: #2e5ef5;
    border-radius: 0.8 * @px-10;
    text-align: center;
    color: #fff;
  }
}

.mul-modal {
  position: fixed;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  // z-index: 2;
  z-index: 1;
  &-dw {
    background-image: url("../img/hc-success.png");
    width: 64 * @px-10;
    height: 66 * @px-10;
    background-repeat: no-repeat;
    background-size: 100% 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    // z-index: 4;
    &-text {
      font-size: 3 * @px-10;
      text-align: center;

      p {
        margin-top: 1.6 * @px-10;
      }
      .dddd {
        margin-top: 0.4 * @px-10;
      }
      p:first-child {
        font-size: 4 * @px-10;
        margin-top: 6 * @px-10;
      }
      p:last-child {
        font-size: 8 * @px-10;
        margin-top: 8 * @px-10;
      }
    }
    &-btn {
      // z-index: 4;
      margin-bottom: 3.6 * @px-10;
      width: 50 * @px-10;
      height: 10 * @px-10;
      line-height: 10 * @px-10;
      border-radius: 4 * @px-10;
      text-align: center;
      font-size: 3.4 * @px-10;
    }
  }
  &-qq {
    position: absolute;
    top: 0;
    right: 0;
    &-img {
      height: 20.2 * @px-10;
      width: 36 * @px-10;
    }
  }
}
